.dividing-line {
  position: absolute;
  left: 0;

  transform: rotate(180deg);

  width: 100%;
  height: 7px;

  fill: $gray;


  // --- Настройки для правильного расположения линий в разных секциях ---
  &--after-knitted-baskets {
    bottom: -42px;
  }

  &--after-reviews {
    bottom: -51px;
  }
}


/*
=======================================================
=== МЕДИАВЫРАЖЕНИЯ: стили для разных ширин вьюпорта ===
=======================================================
*/

// --- 1) Медиа для "Планшетной" версии сайта ---
@media (min-width: $width--tablet) {
  .dividing-line {
    width: 772px;
    height: 17px;


    // --- Настройки для правильного расположения линий в разных секциях ---
    &--after-knitted-baskets {
      bottom: -63px;
      left: -52px;
    }

    &--after-reviews {
      left: -2px;
      bottom: -83px;
    }
  }


  /* ------------------------------------------------------------------- */
  // --- Конец медиа для "Планшетной" версии ---
}


// *** 2) Медиа для "Десктопной" версии сайта ***
@media (min-width: $width--desktop) {
  .dividing-line {
    left: 0;

    width: 1050px;
    height: 23px;


    // --- Настройки для правильного расположения линий в разных секциях ---
    &--after-knitted-baskets {
      bottom: -96px;
    }

    &--after-reviews {
      bottom: -90px;
    }
  }

  /* ------------------------------------------------------------------- */
  // --- Конец медиа для "Десктопной" версии ---
}
